<template>
  <v-menu origin="center center">
    <template v-slot:activator="{ on, attrs }">
      <v-btn
        depressed
        block
        v-on="on"
        v-bind="attrs"
        color="grey lighten-2"
        class="justify-start mb-3">
        <v-icon class="mr-3">mdi-content-copy</v-icon>
        salin ke
      </v-btn>
    </template>
    <v-list link>
      <v-list-item-group>
        <v-list-item
          v-for="(item, index) in dataList"
          :key="index"
          @click="copyCard(item.id)">
          <v-list-item-title>{{ item.name }}</v-list-item-title>
        </v-list-item>
      </v-list-item-group>
    </v-list>
  </v-menu>
</template>

<script>
  import { copyDataCard } from '@/config/startup/sprint'
  import { mapState } from 'vuex'

  export default {
    props: ['id'],

    data: () => ({
      items: [
        { title: 'Click Me' },
        { title: 'Click Me' },
        { title: 'Click Me' },
        { title: 'Click Me 2' },
      ],
    }),

    computed: {
      ...mapState('dataStartupSprint', ['dataList']),     
    },

    methods: {
      copyCard(val) {
        copyDataCard({
          card_id: this.id,
          list_id: val
        })
          .then(() => {
            this.$store.dispatch('dataStartupSprint/getDataList', this.$route.params.sprint_id)
          })
      }
    }
  }
</script>