<template>
  <v-dialog
    v-model="dialog"
    width="800"
    persistent>
    <template v-slot:activator="{ on }">
      <v-icon v-on="on">mdi-dots-horizontal</v-icon>
    </template>

    <v-card>
      <v-card-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="dialog = false">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-card-title>
      <v-card-text class="black--text">
        <v-row>
          <v-col cols="12" md="8" sm="12">
            <v-text-field
              prepend-icon="mdi-card-text"
              v-model="detailCard.title"
              :append-icon="editName?'mdi-pencil':''"
              @click:append="updateTitle"
              @focus="editName = true"
              @blur="editName = false"
              dense
              class="title font-weight-bold"
            ></v-text-field>
            <div class="d-flex">
              <v-icon>mdi-text</v-icon>
              <div class="title font-weight-bold ml-3">
                Deskripsi
              </div>
            </div>            
            <v-textarea
              class="mt-2 ml-9"
              outlined
              full-width
              v-model="detailCard.description"
              @focus="editDesc = true"
              @blur="editDesc = false"
              :append-icon="editDesc?'mdi-pencil':''"
              @click:append="updateDesc"
            ></v-textarea>            
            <div class="d-flex">
              <v-icon>mdi-attachment</v-icon>
              <div class="title font-weight-bold ml-3">
                Attachment
              </div>
            </div>             
            <v-file-input
              show-size
              counter
              label="File"
              class="ml-9"
              prepend-icon=""
              v-model="file"
              @focus="editFile = true"
              @blur="editFile = false"
              :append-icon="editFile?'mdi-pencil':''"
              @click:append="updateFile"
            ></v-file-input> 
          </v-col>
          <v-col cols="12" md="4" sm="12">
            <p class="title grey--text">ACTIONS</p>
            <Move :id="detailCard.id" />
            <Copy :id="detailCard.id" />            
            <v-btn
              depressed
              block
              color="grey lighten-2"
              class="justify-start mb-3">
              <v-icon class="mr-3">mdi-clock</v-icon>
              due date
            </v-btn>
            <v-btn
              depressed
              block
              color="grey lighten-2"
              class="justify-start mb-3">
              <v-icon class="mr-3">mdi-trash-can</v-icon>
              hapus
            </v-btn>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
  import { mapState } from 'vuex'
  import {
    putDataCardFile,
    putDataCardName,
    putDataCardDesc,
    getDetailCard
  } from '@/config/startup/sprint'
  import Copy from './Copy'
  import Move from './Move'

  export default {
    props: ['data'],

    data () {
      return {
        dialog: false,
        valid: true,
        editName: false,
        editDesc: false,
        editFile: false,
        file: null,
        detailCard: {}
      }
    },

    components: {
      Move,
      Copy
    },

    watch: {
      dialog(val) {
        if (val === true) {
          getDetailCard(this.data.id)
            .then(res => this.detailCard = res.data.data[0])
        }
      }
    },

    computed: {
      ...mapState('dataStartupSprint', ['dataList']),     
    },

    methods: {
      updateFile () {
        const data = new FormData()
        data.append('berkas', this.file)
        putDataCardFile(this.data.id, data)
      },
      updateTitle () {
        putDataCardName(this.data.id, {
          title: this.detailCard.title
        })
      },
      updateDesc () {
        putDataCardDesc(this.data.id, {
          description: this.detailCard.description
        })
      }
    }
  }
</script>